  justify-content: flex-start;
  align-items: center;
}
@media (max-width: 767px) {
  .offers__item {
    padding: 45px 20px 35px;
    min-height: 384px;
  }
}

.offers__item:hover,
.offers__item:active {
  transition: 0.6s;
  outline-offset: -7px;
  border: 7px solid;
  border-image-source: linear-gradient(148.82deg, #d10a42 0.85%, #00359f 100%);
  border-image-slice: 26;
}

.offers__item h3 {
  text-align: center;
  margin: 0;
  margin-bottom: 4px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 26px;
  line-height: 40px;
}
@media (max-width: 767px) {
  .offers__item h3 {
    font-size: 22px;
    line-height: 24px;
    margin-bottom: 9px;
  }
}

.offers__item p {
  text-align: center;
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #495a7d;
}

.offers__price {
  margin-left: 5px;
  margin-top: 66px;
  position: relative;
}
@media (max-width: 767px) {
  .offers__price {
    font-size: 22px;
    line-height: 24px;
    margin-top: 54px;
  }
}

.offers__price b {
  position: relative;
  z-index: 3;
  color: #103AAE;
  font-weight: 700;
  font-size: 60px;
  line-height: 90px;
}
@media (max-width: 767px) {
  .offers__price b {
    font-size: 48px;
    line-height: 72px;
  }
}

.offers__price span {
  user-select: none;
  position: absolute;
  left: -19px;
  bottom: -5px;
  z-index: 2;
  color: #e8e8e8;
  opacity: 0.48;
  font-weight: 700;
  font-size: 100px;
  line-height: 150px;
}
@media (max-width: 767px) {
  .offers__price span {
    left: -16px;
    font-size: 80px;
    line-height: 120px;
  }
}

.offers__item svg {
  position: relative;
  z-index: 3;
  margin-left: 11px;
}
@media (max-width: 767px) {
  .offers__item svg {
    width: 24px;
    height: 33px;
    margin-left: 7px;
  }
}

.offers__link {
  user-select: none;
  margin-top: 33px;
  text-align: center;
  transition: 0.3s ease;
  background-color: #ffffff;
  outline: 2px solid #102654;
  width: 206px;
  min-height: 64px;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  padding: 25px 25px 22px;
}
@media (max-width: 1199px) {
  .offers__link {
    margin-top: 35px;
    width: 56%;
  }
}
@media (max-width: 767px) {
  .offers__link {
    width: 83.7%;
    margin-top: 30px;
  }
}
.offers__link:hover, .offers__link:focus {
  color: #ffffff;
  background: linear-gradient(to right, #b80027, #003d85 100%);
  outline: none;
}
.offers__link:active {
  color: rgba(255, 255, 255, 0.7);
  background: linear-gradient(to right, #b80027, #003d85 100%);
  outline: none;
}
.offers__link:disabled {
  color: #ffffff;
  background-color: #ffffff;
  outline: 2px solid #102654;
  opacity: 0.3;
}

